import React from 'react';
import Slider from "react-slick";
import { Fixed_Deposit, Home_Loan, AIF } from '../../../../assets/images';
export default class SubProducts extends React.Component {
    renderSubProducts = () => {
        const elements = [
            {
                src: Home_Loan,
                title: 'Home Loan',
                key: 'Home_Loan',
                description: 'Your dream home awaits',
                link: 'https://www.onebajaj.capital/loans/home-loan/campaign/002/apply-now.html?utm_source=homepage_carousal&utm_medium=icon&utm_content=hl'
            }, {
                src: Fixed_Deposit,
                key: 'Fixed_Deposit',
                title: 'Fixed Deposits',
                description: 'Invest now & get fixed returns',
                link: 'https://www.onebajaj.capital/fixed-deposit/campaign/001/apply-now.html?utm_source=homepage_carousal&utm_medium=icon&utm_content=fd'
            },
            // {
            //     src: PMS,
            //     title: 'Portfolio Management Services',
            //     description: 'Invest in ABM India Consumption Fund',
            //     link: 'https://onebajaj.capital/portfolio-management-services/campaign-001/apply-now-abm-pms.html'
            // },
            {
                src: AIF,
                title: 'Start Monthly SIP Today',
                description: 'To turn into a crorepati tomorrow',
                link: 'https://www.onebajaj.capital/mutual-fund/campaign/001/sip-your-way-to-crorepati.html?utm_source=homepage_carousal&utm_medium=icon&utm_content=sip'
            },
        ];
        return elements.map((item) => {
            return (<div className="iconbox" key={item.title}>
                <a href={item.link} variant="link"
                    onClick={() => this.pushToDataLayer({
                        url: item.link,
                        menu: item.key
                    })}>
                    <div className="img">
                        <div className="row">
                            <div className="col-auto">
                                <img alt="bajaj" src={item.src} />

                            </div>
                            <div className="col">   <h5>{item.title}</h5> {item.description}</div></div></div>
                </a></div>)
        })
    }

    pushToDataLayer = ({ url, menu }) => {
        window.dataLayer.push({
            'event': 'Corousel_click',
            'eventCategory': `${menu}_Click`, // {{dynamic}} contains Corousel_Name i.e. Corousel_Portfolio_Management_Services_Click, Corousel_Fixed_Deposit_Click, Corousel_Alternative_Investment_Fund_Click
            'eventAction': window.location.href, // {{dynamic}} contains current page url i.e stage.onebajaj.capital
            'eventLabel': url, // {{dynamic}} contains the url of page to which user is redirected i.e  www.onebajaj.capital/loans/home-loan/campaign/002/apply-now.html
        });
    }

    render() {
        const settings = {
            dots: false,
            infinite: true,
            speed: 1400,
            slidesToShow: 3,
            slidesToScroll: 1,
            autoplay: true,
            pauseOnHover: true,
            swipeToSlide: true,
            autoplaySpeed: 2000,
            arrows: false,
            responsive: [
                {
                    breakpoint: 1080,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1
                    }
                }, {
                    breakpoint: 800,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };

        return (
            <div className=" container-fluid highlighter">
                <div className="container">
                    <Slider {...settings}>
                        {this.renderSubProducts()}
                    </Slider>
                </div>
            </div>
        );
    }
}