import React from 'react';
import { Link } from "react-router-dom";
import { error_404 } from '../../assets/images';
import Header from '../header/header';

export default () => (
    <Header>
        <section className="aboutbanner errorpage ">
            <img src={error_404} className="img-fluid" alt="error" />
            <div className="container">
                <div className="row">
                    <div className="col-md-8 offset-md-2 sec-heading text-center pt100">
                        <h1>Uh-Oh!</h1>
                        'Something doesn't seem right.
        Let's take you back</div>
                    <div className="col-md-8 offset-md-2  text-center">
                        <Link to="/" className="btn btn-primary">  HOME </Link>
                    </div>
                </div>
            </div>
        </section>
    </Header>
)