import React from 'react';
import Slider from "react-slick";
import { Carousel, Image } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {
    mutual_funds, car_insurance,
    term_insurance, credit_card, health_insurance, personal_loan,
    bajaj_capital_loan,
    bajaj_mutual_fund, onebajaj_credit_card,
    onebajaj_main, home_bajajcapital_insurance, black
} from '../../../../assets/images';
import urlConstants from '../../../../constants/url';

export default class Products extends React.Component {
    constructor(props) {
        super(props);
        this.timer = false;
        this.bgImages = [
            {
                picture: bajaj_mutual_fund,
                mobilePicture: black,
                title: 'Experience hassle-free investing with Bajaj Capital',
                description: 'Invest in mutual funds today and build an intelligent diversified portfolio that helps you fulfil your dreams',
                buttonText: 'Find out more',
                href: `${urlConstants.mutualFund.link}?utm_source=homepage_banner&utm_medium=label&utm_content=mf`,
            },
            {
                picture: onebajaj_main,
                mobilePicture: black,
                title: 'Your trusted financial partner for over 54 years',
                description: 'Join lakhs of people who have put their faith in our services to help them meet their financial goals',
                buttonText: 'Read more',
                link: '/about?utm_source=homepage_banner&utm_medium=label&utm_content=about',
            }, {
                picture: onebajaj_credit_card,
                mobilePicture: black,
                title: 'More rewarding than you would ever believe',
                description: 'From exciting cashback offers to travel benefits, compare and apply for the credit card that is right for you',
                buttonText: 'Explore now',
                href: `${urlConstants.creditCard.link}?utm_source=homepage_banner&utm_medium=label&utm_content=cc`,
            }, {
                picture: bajaj_capital_loan,
                mobilePicture: black,
                title: 'No delays in fulfilling your bucket list any longer',
                description: 'Avail personal loan online at the most attractive interest rate from top banks in India through Bajaj Capital',
                buttonText: 'Get started',
                href: `${urlConstants.personalLoan.link}?utm_source=homepage_banner&utm_medium=label&utm_content=pl`,
            }, {
                picture: home_bajajcapital_insurance,
                mobilePicture: black,
                title: `Secure your family's happiness today`,
                description: 'Getting yourself and your family insured is the first step towards building your future. Choose your policy today.',
                buttonText: 'Get started',
                href: `${urlConstants.insurance.link}?utm_source=homepage_banner&utm_medium=label&utm_content=insurance`,
            }
        ];
        this.state = {
            index: 0,
            direction: null,
            autoplay: true,
            isMobile: (window.innerWidth <= 650)
        }
    }

    handleWindowResize = () => {
        this.setState({
            isMobile: (window.innerWidth <= 650)
        })
    };

    componentDidUpdate() {
        window.addEventListener('resize', this.handleWindowResize);
        return () => window.removeEventListener('resize', this.handleWindowResize);
    }

    changeOrientation = () => {
        this.setState({
            isMobile: (window.innerWidth <= 600)
        });
    }

    componentDidMount() {
        this.timer = setTimeout(() => {
            this.setState({ autoplay: false }, () => {
                this.slider.slickGoTo(0);
            })
        }, 10000);
    }

    componentWillUnmount() {
        clearTimeout(this.timer);
    }

    handleSelect = (selectedIndex, e) => {
        this.setState({
            index: selectedIndex,
            direction: e.direction,
        });
    }

    pushToDataLayer = ({ buttonText, url, home = false }) => {
        const event = buttonText === 'Read more' ? 'Read_more' : 'Find_out_more';
        const route = home ? `${window.location.href}${url.substring(1)}` : url;
        window.dataLayer.push({
            event,
            'eventCategory': `${event}_click`,
            'eventAction': window.location.href, // first {{dynamic}} contains the navigation tab selected. Second {{dynamic}} conatins drop down selected i.e i.e. Investments_Click|Mutual Funds_select, Investments_Click|Fixed Deposit_select'eventLabel': '{{dynamic}}', // {{dynamic}} contains the url of page to which user is redirected i.e stage.onebajaj.capital/about
            'eventLabel': route, // {{dynamic}} contains the url of page to which user is redirected i.e stage.onebajaj.capital/about  
        });
    }

    dataLayerProduct = ({ buttonText, url }) => {
        window.dataLayer.push({
            event: 'Product_logo_click',
            eventCategory: `${buttonText}_Click`, // {{dynamic}} contains Product Logo Name i.e. Mutual_Funds_Click, Credit_Card_Click, Motor_Insurance_Click
            'eventAction': window.location.href, // {{dynamic}} contains current page url i.e stage.onebajaj.capital
            'eventLabel': url, // {{dynamic}} contains the url of page to which user is redirected i.e stage.onebajaj.capital/about
        })
    }

    render() {
        const { autoplay, isMobile, index, direction } = this.state;
        const settings = {
            dots: false,
            infinite: false,
            speed: 1500,
            slidesToShow: 6,
            slidesToScroll: 1,
            autoplay,
            autoplaySpeed: 2000,
            arrows: false,
            swipeToSlide: true,
            // pauseOnHover: true,
            responsive: [
                {
                    breakpoint: 1360,
                    settings: {
                        slidesToShow: 5,
                        slidesToScroll: 1,
                    }
                }, {
                    breakpoint: 1200,
                    settings: {
                        slidesToShow: 4,
                        slidesToScroll: 1,
                    }
                },
                {
                    breakpoint: 1100,
                    settings: {
                        slidesToShow: 4,
                        slidesToScroll: 1
                    }
                },
                {
                    breakpoint: 850,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1
                    }
                }
            ]
        };
        const [item1, item2, item3, item4, item5] = this.bgImages;
        return (
            <div className="jumbotron jumbotron-fluid homebanner" id="header1">
                <Carousel
                    activeIndex={index}
                    direction={direction}
                    onSelect={this.handleSelect}
                    controls={!isMobile}
                    indicators={false}
                    fade={true}
                    interval={8000}
                >
                    <Carousel.Item >
                        <div className="carousel-item active" id="dashboard">
                            <Image src={isMobile ? item1.mobilePicture : item1.picture}
                                fluid
                                className={`w-100 img-fluid`} alt="..." />
                            <div className="container">
                                <div className="carousel-caption">
                                    <div className="row">
                                        <div className="col-md-9 col-lg-8">
                                            <h1>{item1.title}</h1>
                                            <p>{item1.description} </p>
                                            {item1.link && (<Link to={item1.link} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item1.link,
                                                buttonText: item1.buttonText,
                                                home: true
                                            })}> {item1.buttonText} </Link>)}
                                            {item1.href && (<a href={item1.href} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item1.href,
                                                buttonText: item1.buttonText,
                                            })}> {item1.buttonText} </a>)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item >
                        <div className="carousel-item active" id="dashboard">
                            <Image src={isMobile ? item2.mobilePicture : item2.picture}
                                fluid
                                className={`w-100 img-fluid`} alt="..." />
                            <div className="container">
                                <div className="carousel-caption">
                                    <div className="row">
                                        <div className="col-md-9 col-lg-8">
                                            <h1>{item2.title}</h1>
                                            <p>{item2.description} </p>
                                            {item2.link && (<Link to={item2.link} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item2.link,
                                                buttonText: item2.buttonText,
                                                home: true
                                            })}> {item2.buttonText} </Link>)}
                                            {item2.href && (<a href={item2.href} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item2.href,
                                                buttonText: item2.buttonText,
                                            })}> {item2.buttonText} </a>)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item >
                        <div className="carousel-item active" id="dashboard">
                            <Image src={isMobile ? item3.mobilePicture : item3.picture}
                                fluid
                                className={`w-100 img-fluid`} alt="..." />
                            <div className="container">
                                <div className="carousel-caption">
                                    <div className="row">
                                        <div className="col-md-9 col-lg-8">
                                            <h1>{item3.title}</h1>
                                            <p>{item3.description} </p>
                                            {item3.link && (<Link to={item3.link} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item3.link,
                                                buttonText: item3.buttonText,
                                                home: true
                                            })}> {item3.buttonText} </Link>)}
                                            {item3.href && (<a href={item3.href} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item3.href,
                                                buttonText: item3.buttonText,
                                            })}> {item3.buttonText} </a>)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item >
                        <div className="carousel-item active" id="dashboard">
                            <Image src={isMobile ? item4.mobilePicture : item4.picture}
                                fluid
                                className={`w-100 img-fluid`} alt="..." />
                            <div className="container">
                                <div className="carousel-caption">
                                    <div className="row">
                                        <div className="col-md-9 col-lg-8">
                                            <h1>{item4.title}</h1>
                                            <p>{item4.description} </p>
                                            {item4.link && (<Link to={item4.link} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item4.link,
                                                buttonText: item4.buttonText,
                                                home: true
                                            })}> {item4.buttonText} </Link>)}
                                            {item4.href && (<a href={item4.href} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item4.href,
                                                buttonText: item4.buttonText,
                                            })}> {item4.buttonText} </a>)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item >
                        <div className="carousel-item active" id="dashboard">
                            <Image src={isMobile ? item5.mobilePicture : item5.picture}
                                fluid
                                className={`w-100 img-fluid`} alt="..." />
                            <div className="container">
                                <div className="carousel-caption">
                                    <div className="row">
                                        <div className="col-md-9 col-lg-8">
                                            <h1>{item5.title}</h1>
                                            <p>{item5.description} </p>
                                            {item5.link && (<Link to={item5.link} className="btn btn-primary" onClick={() => this.pushToDataLayer({
                                                url: item5.link,
                                                buttonText: item5.buttonText,
                                                home: true
                                            })}> {item5.buttonText} </Link>)}
                                            {item5.href && (<a href={item5.href} className="btn btn-primary" target="_blank" rel="noopener noreferrer" onClick={() => this.pushToDataLayer({
                                                url: item5.href,
                                                buttonText: item5.buttonText,
                                            })}> {item5.buttonText} </a>)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Carousel.Item>
                </Carousel>
                <div className="container pro-slide">
                    <div className="row">
                        <div className="col-12">
                            <ul className="banner-slide">
                                <Slider ref={slider => (this.slider = slider)} {...settings}>
                                    <li>
                                        <a href={`${urlConstants.mutualFund.link}?utm_source=homepage_banner&utm_medium=icon&utm_content=mf`} variant="link"
                                            className=" text-decoration-none"
                                            onClick={() => this.dataLayerProduct({
                                                url: `${urlConstants.mutualFund.link}?utm_source=homepage_banner&utm_medium=icon&utm_content=mf`,
                                                buttonText: 'Mutual_Funds'
                                            })}>
                                            <span>
                                                <img src={mutual_funds} className="img-fluid" alt="credit" /> </span>
                                            {urlConstants.mutualFund.text}
                                        </a>
                                    </li>
                                    <li>
                                        <a href={`${urlConstants.creditCard.link}?utm_source=homepage_banner&utm_medium=icon&utm_content=cc`} variant="link"
                                            className=" text-decoration-none"
                                            onClick={() => this.dataLayerProduct({
                                                url: `${urlConstants.creditCard.link}?utm_source=homepage_banner&utm_medium=icon&utm_content=cc`,
                                                buttonText: 'Credit_Cards'
                                            })}
                                        >
                                            <span>  <img src={credit_card} alt="credit" /> </span>{urlConstants.creditCard.text}
                                        </a>
                                    </li>
                                    <li>
                                        <a href={`${urlConstants.motorInsurance.link}/?utm_source=homepage_banner&utm_medium=icon&utm_content=mi`} variant="link"
                                            target="_blank" rel="noopener noreferrer"
                                            className=" text-decoration-none"
                                            onClick={() => this.dataLayerProduct({
                                                url: `${urlConstants.motorInsurance.link}/?utm_source=homepage_banner&utm_medium=icon&utm_content=mi`,
                                                buttonText: 'Motor_Insurance'
                                            })}
                                        >
                                            <span>  <img src={car_insurance} alt="motor" /> </span>
                                            {urlConstants.motorInsurance.text}
                                        </a>
                                    </li>
                                    <li>
                                        <a href={`${urlConstants.healthInsurance.link}/?utm_source=homepage_banner&utm_medium=icon&utm_content=hi`} variant="link"
                                            target="_blank" rel="noopener noreferrer"
                                            className=" text-decoration-none"
                                            onClick={() => this.dataLayerProduct({
                                                url: `${urlConstants.healthInsurance.link}/?utm_source=homepage_banner&utm_medium=icon&utm_content=hi`,
                                                buttonText: 'Health_Insurance'
                                            })}
                                        >
                                            <span><img src={health_insurance} alt="health" /> </span>  {urlConstants.healthInsurance.text}
                                        </a>
                                    </li>
                                    <li>
                                        <a href={`${urlConstants.termInsurance.link}/?utm_source=homepage_banner&utm_medium=icon&utm_content=ti`} variant="link"
                                            target="_blank" rel="noopener noreferrer"
                                            className=" text-decoration-none"
                                            onClick={() => this.dataLayerProduct({
                                                url: `${urlConstants.termInsurance.link}/?utm_source=homepage_banner&utm_medium=icon&utm_content=ti`,
                                                buttonText: 'Term_Insurance'
                                            })}
                                        >
                                            <span> <img src={term_insurance} alt="term" /></span>   {urlConstants.termInsurance.text}
                                        </a>
                                    </li>
                                    <li>
                                        <a href={`${urlConstants.personalLoan.link}?utm_source=homepage_banner&utm_medium=icon&utm_content=pl`} variant="link"
                                            className=" text-decoration-none"
                                            onClick={() => this.dataLayerProduct({
                                                url: `${urlConstants.personalLoan.link}?utm_source=homepage_banner&utm_medium=icon&utm_content=pl`,
                                                buttonText: 'Personal_Loan'
                                            })}
                                        >
                                            <span> <img src={personal_loan} alt="personal" /></span>  {urlConstants.personalLoan.text}
                                        </a>
                                    </li>
                                </Slider>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}