import React from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from "react-router-dom";
import Dashboard from '../pages/dashboard/dashboard';
import About from '../pages/about/about';
import AboutLeadership from '../pages/aboutLeadership/aboutLeadership';
import Blogs from '../pages/blogs/blogs';
import BlogDetail from '../pages/blogDetail/blogDetail';
import BranchLocator from '../pages/branchLocator/branchLocator';
import Header from '../components/header/header';
import NoPageFound from '../components/404';
import Footer from '../components/footer/footer';
import ScrollToTop from './component/scrollToTop';
import Terms from '../pages/terms/terms';

const HeaderRoute = (props) => {
    const { component, path } = props;
    const Component = component;
    return (
        <React.Fragment>
            <Header>
                <Route path={path} {...props} render={(prop) => (
                    <Component {...prop} />
                )} />
            </Header>
            <Footer />
        </React.Fragment>
    )
}

const AppRouter = () => {
    return (
        <React.Fragment>
            <Router>
                <ScrollToTop>
                    <Switch>
                        <HeaderRoute exact path="/" component={Dashboard} />
                        <HeaderRoute exact path="/about" component={About} />
                        <HeaderRoute exact path="/about-leadership" component={AboutLeadership} />
                        <HeaderRoute exact path="/terms/:type" component={Terms} />
                        <HeaderRoute exact path="/blogs" component={Blogs} />
                        <HeaderRoute exact path="/blog-detail/:id" component={BlogDetail} />
                        <HeaderRoute exact path="/branch-locator" component={BranchLocator} />
                        <Route exact path="/404" component={NoPageFound} />
                        <Route exact path="*" >
                            <Redirect to="/" />
                        </Route>
                    </Switch>
                </ScrollToTop>
            </Router>
        </React.Fragment>

    )
}

export default AppRouter;